import React from 'react';
import Header from './pages/Header';
import Home from './pages/Home';
import Doc from './pages/Doc';
import Tuto from './pages/Tuto';
import Blog from './pages/Blog';
import Comm from './pages//Comm';
import Login from './pages/Login';
import routes from './router.config';
import {Button} from 'antd-mobile';
import { Route,Redirect,Router,Switch } from 'react-router-dom';

function PrivateRoute({ component:Com,...rest}){
    return (
        <Route
        {...rest}
        render={({location}) =>
            localStorage.getItem('userinfo')
            ?<Com />
            :(
                <Redirect to={{ 
                        pathname:"/login",
                        state:location
                    }}
                />
            )
        }
        />
    )
}
function RouteWithSubRoutes(routes){
    return (
        <Route path={routes.path}
                render={props => (
                    <routes.component {...props} routes={routes.routes} />
                )}
        />
    )
}
const App = ()=>{
    // return <div>
    //     <Button type="primary">开始使用</Button>
    // </div>
	return <Router>
		<Header />
		<Switch>
            {/* {
                routes.map(({pri,...rest}) =>{
                    // <Route exact={route.exact} path={route.path} component={route.component}/>
                    if(pri){
                        return <PrivateRoute {...rest} />
                    }   
                    return <Route {...rest}/>
                })
            } */}
			<Route path='/' component={Home} />
			<PrivateRoute path='/doc' component={Doc} />
			<PrivateRoute path='/tuto' component={Tuto} />
			<Route path='/blog' component={Blog} />
			<Route path='/comm' component={Comm} />
			<Route path='/login' component={Login} />
		</Switch>
	</Router>
}
export {PrivateRoute,RouteWithSubRoutes};
export default App;

