import React, { useEffect } from 'react'
import {
    NavLink, Route, Redirect,
    useHistory,
    useLocation,
    useParams,
    useRouteMatch,
    withRouter,
    Switch
} from 'react-router-dom'
import './doc.css';
import {NavBar,Icon} from 'antd-mobile'
import { PrivateRoute, RouteWithSubRoutes } from '../App';
import routes from '../router.config'
const DocContent = (props) => {
    return <h1>
        { props.match.params.type }
    </h1>
}

const Doc = (props) => {
    console.log(props)
    useEffect(()=>{
        if(!localStorage.getItem('userinfo')){
            props.history.push({
                pathname:'/login',
                state:props.location
            })
        }
    },[])

    // const history = useHistory();
    // const location = useLocation();
    // const par = useParams();
    // const mat = useRouteMatch();
    // console.log(history);
    // console.log(location);
    // console.log(par);
    // console.log(mat);
    // useEffect(()=>{
    //     if(!localStorage.getItem('userinfo')){
    //         props.history.push({
    //             pathname:'/login',
    //             state:props.location
    //         })
    //     }
    // },[])
    return (
        <div className='doc'>
            {/* <NavBar
                mode="light"
                icon={ <Icon type="left" /> }
                onLeftClick={ () => props.history.goBack() }
                rightContent={ [
                    <Icon key="0" type="search" style={ { marginRight: '16px' } } />,
                    <Icon key="1" type="ellipsis" />,
                ] }
            >NavBar</NavBar> */}
            <div className="content">
                {/* 嵌套路由 */ }
                {/* 直接返回Route：
                 <Route path='/doc/:type' component={ DocContent } /> 
                */}
                <Switch>
                    {routes[1].routes.map((route,idx) => {
                        if(route.pri){
                            return(
                                <PrivateRoute key={idx} path={route.path} component={DocContent}>
                                    <route.component />
                                </PrivateRoute>
                            )
                        }else{
                            return(
                                <RouteWithSubRoutes key={idx} {...route}></RouteWithSubRoutes>
                            )
                        }
                    })}
                </Switch>
                <Redirect from='/doc' to='/doc/core' />
            </div>
            <ol>
                <li><NavLink to='/doc/core'>1.核心概念</NavLink></li>
                <li><NavLink to='/doc/guide'>2.高级指引</NavLink></li>
                <li><NavLink to='/doc/api'>3.API</NavLink></li>
                <li><NavLink to='/doc/hooks'>4.Hooks</NavLink></li>
            </ol>
        </div>
    )
}

export default withRouter(Doc);