import React from 'react';
import {BrowserRouter as Router,Redirect,Route,NavLink,Switch} from 'react-router-dom'
import Home from './pages/Home'
import Doc from './pages/Doc'
import Header from './pages/Header'
import Tuto from './pages/Tuto'
import Blog from './pages/Blog'
import Comm from './pages/Comm'
import Login from './pages/Login'

import Core from './routing/Core';
import Api from './routing/Api';
import Guide from './routing/Guide';
import Hooks from './routing/Hooks'
const routes = [
    {
        path:'/',
        exact:true,
        component:Home
    },
    {
        path:'/',
        component:Doc,
        pri:true,
        routes:[
            {
                path:'/doc/core',
                component:Core,
                pri:true
            },
            {
                path:'/doc/guide',
                component:Guide,
                pri:true
            },
            {
                path:'/doc/api',
                component:Api,
                pri:true
            },
            {
                path:'/doc/hooks',
                component:Hooks,
                pri:true
            }
        ]
    }
]
